<template>
    <!-- div :style="{ 'padding-right': era*4 + 'vw' }">
        <v-row class="bg-light">
            <v-col cols="6" class=" py-0">
                <avatar-and-name :isImg="false" :named="the_name"></avatar-and-name>
            </v-col>

            <v-col cols="6" class=" py-0 pt-1 text-right">
                <v-sheet class="d-inline pa-2">
                    &nbsp;&nbsp;&nbsp;&nbsp;<span class="text-h8">买入(件)：</span>

                    <v-btn icon x-small @click="changeNum(true)">
                        <v-icon>mdi-plus</v-icon>
                    </v-btn>

                    <span :class="{ 'orange--text': num_color }">&nbsp;{{ num }}&nbsp;</span>

                    <v-btn icon x-small @click="changeNum(false)">
                        <v-icon>mdi-minus</v-icon>
                    </v-btn>
                </v-sheet>
            </v-col>
        </v-row>
    </div -->
    <div class="d-inline">

        <v-sheet class="d-inline">
            &nbsp;&nbsp;&nbsp;&nbsp;<span class="sub-header">买入：</span>

            <v-btn icon x-small @click="changeNum(true)">
                <v-icon>mdi-plus</v-icon>
            </v-btn>

            <span :class="{ 'orange--text': num_color }">&nbsp;{{ num }}&nbsp;(件)</span>

            <v-btn icon x-small @click="changeNum(false)">
                <v-icon>mdi-minus</v-icon>
            </v-btn>
        </v-sheet>
    </div>
</template>

<script>
import AvatarAndName from '@/components/User/AvatarAndName.vue'
    export default {
  components: { AvatarAndName },
        props: {
            the_name: {
                type: String,
                default: '第1代'
            },
            era: {
                type: Number,
                default: 0
            },
            the_item: {
                type: Object
            }
        },
        name: '',
        data() {
            return {
                num: 0
            }
        },
        mounted() {
            this.num = this.the_item.product[0].num
        },
        computed: {
            num_color() {
                if (this.num > 0) {
                    return true
                }
                return false
            }
        },
        methods: {
            changeNum(plus_or_minus) {

                if (plus_or_minus) {
                    this.num += 1
                } else {
                    if (this.num > 0) {
                        this.num -= 1
                    }
                }

                this.the_item.product[0].num = this.num
            },
            locationNum() {

            }
        }
    }
</script>

<style lang="sass" scoped>
    
</style>


