<template>
    <content-layout>
        <div slot="content">

            <active-house-bonus-tree ref="countingTree"></active-house-bonus-tree>
        </div>

        <div slot="option">

            <v-select :items="limit_pvs" v-model="limit_pv" :label="'及格线'" color="blue-grey"></v-select>
            <v-select :items="bonus_usd" v-model="usd" :label="'奖励金额'" color="blue-grey"></v-select>
            <v-select :items="users" v-model="user" :label="'结算对象'" color="blue-grey"></v-select>

            <v-btn block @click="numResult"
                color="blue-grey white--text mt-4">立刻结算</v-btn>

        </div>

        <div slot="result">

            <div class="pl-4 res-text">美金(usd)：<strong class="orange--text">{{ bonus }}</strong></div>
            
        </div>
    </content-layout>
    <!-- v-container fluid>
        <v-row>
            <v-col cols="9" class="pr-0">
                
                <v-card class="py-4">
                    
                    <active-house-bonus-tree ref="countingTree"></active-house-bonus-tree>
                </v-card>
                
            </v-col>
            <v-col col="3">
                <v-card class="pa-4">
                    <v-subheader class="pa-0">所买商品：</v-subheader>
                    <div class="">
                        <div>ROSE ESSENTIAL OIL SET</div>
                        <div>价格：<strong class="orange--text">$838.00</strong></div>
                        <div>PV: <strong class="orange--text">&nbsp;&nbsp;60</strong></div>
                    </div>
                    
                </v-card>
                <v-card class="pa-4 mt-5">

                    <v-subheader class="pa-0">操作：</v-subheader>

                    <v-select :items="limit_pvs" v-model="limit_pv" :label="'及格线'" color="blue-grey"></v-select>
                    <v-select :items="bonus_usd" v-model="usd" :label="'奖励金额'" color="blue-grey"></v-select>
                    <v-select :items="users" v-model="user" :label="'结算对象'" color="blue-grey"></v-select>

                    <v-btn block @click="numResult"
                        color="blue-grey white--text mt-4">立刻结算</v-btn>
                </v-card>
                <v-card class="mt-5 pa-4">
                    
                    <v-subheader class="pa-0">结算结果：</v-subheader>
                    <div class="pl-4 res-text">美金(usd)：<strong class="orange--text">{{ bonus }}</strong></div>
                </v-card>
            </v-col>
        </v-row>
    </v-container -->
</template>

<script>
import ContentLayout from '@/components/Layout/ContentLayout.vue'
import ActiveHouseBonusTree from './Inner/ActiveHouseBonusTree.vue'
import AhbTreeItem from './Inner/AhbTreeItem.vue'

    export default {
  components: {ActiveHouseBonusTree, AhbTreeItem, ContentLayout },
        name: '',
        data() {
            return {
                usd: 130,
                limit_pv: 60,
                user: 0,

                limit_pvs: [ {
                    text: '60pv', value: 60
                }, {
                    text: '120pv', value: 120
                } ],

                bonus_usd: [ 100, 130, 150 ],

                users: [ {
                    text: '第0代', value: 0
                }, {
                    text: '第1代A', value: 1
                }],

                bonus: '$0'
            }
        },
        watch: {
            bonus(n, o) {
                if (isNaN(n)) {

                } else {
                    this.bonus = '$' + this.bonus
                }
            }
        },
        methods: {
            numResult() {

                this.bonus = '计算中...'
                const e = this.$refs.countingTree.numResult(this.usd, this.limit_pv, this.user)
                setTimeout(() => {

                    this.bonus = e
                }, 300)
            }
        }
    }
</script>

<style lang="sass" scoped>
    .min-h
        min-height: 65vh
    
</style>


