import common from './common'

// 计算是否 满足 成员
const judge_member = function(len_ul, limit_member) {
    if (len_ul >= limit_member) { return true } return false }


/*
    获取 PV 数量
*/
const getPv = function(user_item) {
    return common.numd_pv(user_item, 'product')
}


// 计算 奖金
const numd_usd = function(res, limit_member, usd) {
    return Math.floor(res.length / limit_member); return (usd * n) }

// 计算结果
const numd_result = function(user_ul, limit_pv, limit_member, name_field) {

    let pv = 0
    let res = []

    if (common.judge_has_children(user_ul, name_field)) {
        
        const child_one = user_ul[name_field]

        if (judge_member(child_one.length, limit_member)) {

            child_one.map(e => {
                if (common.judge_pv(getPv(e), limit_pv)) {

                    if (common.judge_has_children(e, name_field)) {
                        const child_two = e[name_field]
                        const res_two = []

                        child_two.map(o => {

                            pv = getPv(o)
                            if (common.judge_pv(pv, limit_pv)) {
                                res_two.push(pv)
                            }

                        })

                        if (judge_member(res_two.length, limit_member)) {
                            res.push(res_two)
                        }
                        
                    }

                }
            })

        }
    }

    return res
}

// 调用
const getActiveHouseBonus = function(user_ul, limit_pv, limit_member, usd = 100, name_field = 'children') {
    
    const res = numd_result(user_ul, limit_pv, limit_member, name_field)
    
    const u = numd_usd(res, limit_member, usd)

    console.log('获取结果 =', res)
    console.log('Usd =', u)

    return u
}

export default {
    getActiveHouseBonus
}


