<template>
    <v-row class="pa-4 px-6">
        <v-col cols="5">

            <user-model-select @getUser_Father="getUser"></user-model-select>
        </v-col>
        <v-col cols="7">

            <avatar-and-name></avatar-and-name>
        </v-col>

        <v-col cols="12" class="pt-0">

            <!-- 子类地方 -->
            <v-treeview
            
                :items="items"
                class="mt-3"
            >
                <template v-slot:prepend="{ item, open }">
                    <v-icon :i="item">
                    {{ open ? 'mdi-folder-open' : 'mdi-folder' }}
                    </v-icon>
                </template>
                <template v-slot:append="{ item, open }">
                    <ahb-tree-item :the_item="item" :o="open" :era="item.id.length"></ahb-tree-item>
                </template>
            </v-treeview>
            <!-- 子类结束 -->

        </v-col>
    </v-row>
    
</template>

<script>
import getUserData from '@/apis/mock/user_data'

import bah from '@/common/es/bonus/bonusApi/bonus_active_house'

import AvatarAndName from '@/components/User/AvatarAndName.vue'
import AhbTreeItem from './AhbTreeItem.vue'
import UserModelSelect from '@/components/Form/UserModelSelect.vue'
    export default {
  components: { AvatarAndName, AhbTreeItem, UserModelSelect },
        name: '',
    
        data() {
            return {
                items: [ ]
            }
        },
        mounted() {
            this.getUser()
        },
        methods: {
            
            numResult(usd, limit_pv, user) {
                
                if (user == 0) {
                    return bah.getActiveHouseBonus({ children: this.items }, limit_pv, 3)
                }
                return bah.getActiveHouseBonus(this.items[0], limit_pv, 3)
                
            },

            getUser(code = 's') {
                const name = 'activehouse'
                this.items = getUserData(name, code)
            }
        }
    }
</script>

<style lang="sass" scoped>
    
</style>


